<template>
  <div class="search-view">
    <header class="search-view-top">
      <div class="search-bar">
        <div class="search-bar-tool"><input type="text" v-model="searchContext" placeholder="搜索"></div>
      </div>
      <div class="cancel" @click="returnPrev">取消</div>
    </header>
    <div class="search-content">
      <tab-view :action="tabAction" @changeTab="changeTab" :activeId="tabId"></tab-view>
      <!-- 历史记录 -->
      <div class="search-history" v-if="searchList.length==0">
        <header class="search-history-header">搜索<span class="icon iconfont icon-delete clear-history"></span></header>
        <div class="search-history-content">
          <span v-for="(item,key) in searchResult">{{item}}</span>
        </div>
      </div>
      <!-- 搜索结果   -->
      <div class="result-content">
         <div class="hi-life-list-item" v-for="(item,key) in searchList" @click="view(item)">
          <div class="hi-life-list-item-img"><img :src="item.imageUrl"></div>
          <div class="hi-life-list-item-content">
            <h3>{{item.name}}</h3>
            <div class="hi-life-list-item-summary">{{item.summary}}</div>
            <div class="hi-life-list-item-other" v-if="tabId == '1'">￥<span class="int-string">{{item.sellPrice}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import tab from '@/components/tab/tab'
  import {getProductLikeName, getStoreLikeName} from '../../service/getData'
  export default {
    components: {
      'tab-view': tab
    },
    data () {
      return {
        searchContent: [],
        searchList: [],
        searchContext: '',
        searchResult: [],
        tabId: '1',
        tabAction: [{
          id: '1',
          categoryName: '商品'
        },
        {
          id: '2',
          categoryName: '商家'
        }
        ]
      }
    },
    methods: {
      returnPrev: function () {
        this.$router.push({
          name: 'home'
        })
      },
      view: function (val) {
        if (val.spuId !== undefined) {
          this.$router.push({
            name: 'good',
            query:{
              skuId: val.spuId,  //skuId
              latitude: localStorage.latitude,
              longitude: localStorage.longitude
            }
          })
        } else {
          this.$router.push({
            name: 'store',
            query:{
              storeId:val.id,
              latitude: val.latitude,
              longitude: val.longitude
            }
          })
        }
      },
      search: function () {
        this.$toast('搜索成功!')
        var resultArr = []
        if (!localStorage.searchResult) {
          localStorage.setItem('searchResult', this.searchContext)
        } else {
          this.searchResult = localStorage.getItem('searchResult').split(',')
          resultArr = localStorage.getItem('searchResult').split(',')
          if (resultArr.indexOf(this.searchContext) < 0) {
            resultArr.push(this.searchContext)
          }
          resultArr.join('')
          localStorage.setItem('searchResult', resultArr)
        }
      },
      changeTab: function (val) { // 这个是点击tab后可以做的操作
        let _this = this
        this.tabId = val
        this.searchList = []
      }
    },
    created: function () {
      if (localStorage.searchResult) {
        this.searchResult = localStorage.getItem('searchResult').split(',')
      }
    },
    watch: {
      searchContext: function (newValue,oldValue) {
        if (newValue === '') {
          this.searchList = []
          return false
        }
        let _this = this
        if (this.tabId === '1') {
          getProductLikeName(newValue)
          .then(res => {
            if (res.data.length === 0) {
              _this.$toast('没有搜索结果')
            }
            _this.searchList = res.data
            console.log(_this.searchList)
          })
          .catch(res => {
            _this.$toast('搜索失败')
          })
        } else {
          getStoreLikeName(newValue)
          .then(res => {
            if (res.data.length === 0) {
              _this.$toast('没有搜索结果')
            }
            _this.searchList = res.data
            console.log(_this.searchList)
          })
          .catch(res => {
            _this.$toast('搜索失败')
          })
        }
      }
    }
  }
</script>

<style>
/* 搜索页面*/

.search-view {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.search-view header.search-view-top {
  flex-shrink: 0;
  padding-left: .15rem;
  height: .4rem;
  line-height: .4rem;
  display: flex;
  background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 139, 32));
  /* Safari 5.1 - 6.0 */
  background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 139, 32));
  /* 标准的语法（必须放在最后） */
}
.search-view .my-tab{
  flex-shrink: 0;
}

.search-view header input {
  height: 100%;
}
.search-content{
  flex: auto;
  display: flex;
  flex-direction: column;
}

.cancel {
  width: .55rem;
  text-align: center;
  color: #FFFFFF;
}
.result-content{
  flex-shrink: 0;
  flex: auto;
  overflow: auto;
  padding-left: .15rem;
}

.search-history {
  margin-top: .2rem;
  padding: 0 .15rem;
  clear: both;
}

.clear-history {
  float: right;
  width: .16rem;
  height: .16rem;
}

.search-history-content {
  padding: .15rem 0;
}

.search-history-content span {
  display: inline-block;
  padding: .05rem .15rem;
  margin-right: .1rem;
  border-radius: .3rem;
  color: #333;
  background-color: #F5F5F5;
}
</style>


